export class ProductData {
    constructor(events) {
        this.events = events;
    }
    setProduct(items) {
        this.catalog = items;
    }
    getProduct() {
        return this.catalog;
    }
    getTotal(items) {
        let totalPrise = 0;
        items.forEach((item) => (totalPrise = totalPrise + item.price));
        return totalPrise;
    }
}
